.starthistory-container {
  background-color: #dbd3c9;
  display: flex;
  justify-content: center;

  .starthistory-content {
    max-width: 60vw;
    margin-bottom: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .starthistory-img-div {
      width: 80vw;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      margin-top: 5rem;
      margin-bottom: 5rem;
      .starthistory-img-div-content {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 2rem;
      }
      img {
        width: 40%;
        margin-left: 4rem;
      }
    }
    P {
      padding-bottom: 2.7rem;
    }

    h1 {
      padding: 4rem;
      font-size: 3.5rem;

      font-family: Norse;
    }
    h2 {
      padding: 1.5rem;
      font-size: 2.2rem;
    }
    h3 {
      padding: 1rem;
      font-size: 1.3rem;
    }
  }
}
